<template>
    
<div class="stock__item__container">
    <div class="stock__item__img">
        <img  v-lazy="{
            src: this.$store.state.STATIC_URL + this.current_stock.imgsrc
        }"
        alt="">
    </div>
    <div class="stock__item__desc">
        <span>
            {{ this.current_stock.description }}
        </span>
    </div>
</div>

</template>

<script>

import { getStocks } from '../api.js';

export default {
    name: 'StockItemPage',
    props: {
        stock_id: null,
    },
    data () {
        return {
            current_stock: {}
        }
    },
    async beforeMount () {
        console.log('stocks are', this.stocks);
        console.log('current id is', this.stock_id);
        if (this.stocks.length == 0) {
            var stocks = await getStocks();
            this.$store.commit('setStocks', stocks)
        }
        this.setCurrentStock()
    },
    mounted () {
    },
    computed: {
        stocks () {
            return this.$store.state.stocks;
        },
    },
    methods: {
        setCurrentStock () {
            console.log('stocks are', this.stocks);
            var stock = this.stocks.find((item) => 
            item.id == parseInt(this.stock_id))
            this.current_stock = stock;
            console.log('current stock is', stock);
        }
    }
}
</script>