<template>
    <div class="category_container mgt10"
    :id="'category-'+ category.id"
    >
        <div class="category_name">
            {{ category.name }}
        </div>
        <!-- products -->
        <div class="products__list__wrapper">
        <ProductCard
        v-for="product in this.getProductsByCategory(category)"
        v-bind:key="product.id"
        v-bind:product="product"
        >
        </ProductCard>
        <!-- {{ count }} -->
        </div>
        <!-- endof products -->
    </div>
</template>

<script>
import ProductCard from '../components/ProductCard.vue';

export default {
    name: 'ProductCategory',
    props: {
        category: {}
    },
    data () {
        return {
            offsetTop: 0,
        }
    },
    created () {
        window.addEventListener('scroll', this.onScroll);
    },
    unmounted () {
        window.removeEventListener('scroll', this.onScroll);
    },
    components: {
        ProductCard
    },
    computed: {
        products () {
            return this.$store.state.products.filter(
                product => product.parent == null
            )
        },
        activeCategory () {
            return this.$store.state.activeCategory;
        }
    },
    watch: {
        offsetTop () {
        this.callbackFunc()
        }
    },
    methods: {
        getProductsByCategory(category) {
          return this.products.filter(
              pr => pr.category == category.id
          )
        },
        onScroll () {
            // console.clear()
            this.offsetTop = window.pageYOffset;
        },
        isElementInViewport(el) {
            var rect = el.getBoundingClientRect();
            // console.log('rect', rect);
            // console.log('offset top: ', this.offsetTop);
            if (this.offsetTop >= rect.top + window.scrollY && this.offsetTop <= rect.bottom + window.scrollY ) {
                return true;
            } else {
                return false;
            }
        },
        callbackFunc() {
            this.component_position = this.$el.getBoundingClientRect()
            // let items = document.querySelectorAll(".category_container");
            // this.activeCategory = {};
            // for (var i = 0; i < items.length; i++) {
            if (this.isElementInViewport(this.$el)) {
            // items[i].classList.add("in_view");
            this.$store.commit('set_activeCategory', this.category)
            }
            // }
        },
    },
}
</script>