<template>
    <a
    :class="this.isCategoryActive() ? 'active' : ''"
    class="nolink noselect"
    @click="this.goToCategory()"
    >
        {{ category.name }}
    </a>
</template>

<script>
export default {
    name: 'DesktopCategoryItem',
    props: {
        category: {}
    },
    computed: {
        active_category() {
            return this.$store.state.activeCategory;
        }
    },
    methods: {
        isCategoryActive () {
            if (this.category.id == this.active_category.id) {
                return true;
            }
            return false;
        },
        async goToCategory () {
            await this.$router.push('/' + '#category-'+ this.category.id);
            var container = document.getElementById('category-'+ this.category.id)
            container.scrollIntoView({behavior: "smooth"})
        }
    }
}
</script>