        <!-- start user head -->
                <div class="user_head">
            <div class="user_head__block user_head__block1">
                <div class="user_head__block__title">
                    Привет, {{ this.user.name }}
                </div>
                <div class="user_head__block__phone">
                    {{ this.user.phone }}
                </div>
            </div>
            <div class="user_head__block flex flex-center">
                <svg width="50" height="60"
                    xmlns="http://www.w3.org/2000/svg" viewBox="0 0 64 64" aria-labelledby="title" aria-describedby="desc" role="img" xmlns:xlink="http://www.w3.org/1999/xlink"> <path data-name="layer2" d="M38 18s18 14 18 34a10 10 0 0 1-10 10H18A10 10 0 0 1 8 52c0-20 18-34 18-34S14 7.6 14 4s8 2 12 2c5.7 0 6.3-4 10-4s4.1 4.1 10 4.1c2.3 0-2 6-8 11.9z" fill="none" stroke="#fa5523" stroke-miterlimit="10" stroke-width="2" stroke-linejoin="round" stroke-linecap="round"></path> <path data-name="layer2" d="M26 18h13a19.2 19.2 0 0 0 13-4m-2 8a6.5 6.5 0 0 1 6 3" fill="none" stroke="#fa5523" stroke-miterlimit="10" stroke-width="2" stroke-linejoin="round" stroke-linecap="round"></path> <path data-name="layer1" d="M37.9 34a6 6 0 0 0-5.9-5c-3.3 0-6 2.2-6 5.5s3 4.8 6 5.5m-6 5a6 6 0 0 0 6 6c3.3 0 6-2.2 6-5.5s-3-4.8-6-5.5m0-11v-3m0 28v-3" fill="none" stroke="#fa5523" stroke-miterlimit="10" stroke-width="2" stroke-linejoin="round" stroke-linecap="round"></path>
                </svg>
                <div class="user_head__block__bonusinfo uppercase">
                    <div class="user_head__block__bonuscount">
                        {{ this.user.bonus }}
                    </div>
                    бонусов
                </div>
            </div>
        </div>

        <div class="user_head_links">
            <div class="user_head_link pointer noselect"
            :class="this.orders_component_active ? 'active' : ''"
            v-on:click="this.setUserPageComponentActive('orders')">
                <a class="nolink" id="orders_link">
                    Заказы ( {{ this.user.orders.length }} )
                </a>
            </div>
            <div class="user_head_link pointer noselect"
            :class="this.promo_component_active ? 'active' : ''"
            v-on:click="this.setUserPageComponentActive('promo')">
                <a class="nolink" id="promo_link">
                    Промокоды
                </a>
            </div>
            <!-- <div class="user_head_link pointer noselect"
            :class="this.address_component_active ? 'active' : ''"
            v-on:click="this.setUserPageComponentActive('address')">
                <a class="nolink" id="address_link">
                    Адреса
                </a>
            </div>
            <div class="user_head_link pointer noselect"
            :class="this.settings_component_active ? 'active' : ''"
            v-on:click="this.setUserPageComponentActive('settings')">
                <a class="nolink" id="settings_link">
                    Настройки
                </a>
            </div> -->
        </div>
        <!-- eof user head -->
        <div>
            <UserOrders 
            v-show="this.orders_component_active"
            />
            <UserPromo 
            v-show="this.promo_component_active"
            />
            <UserAddress 
            v-show="this.address_component_active"
            />
            <UserSettings 
            v-show="this.settings_component_active"
            />
        </div>

        <div class="open_login_button mgt10 logout_button_container">
            <span class="pointer noselect logout_button_main"
            @click="this.logout_user_clicked()"
            > 
            Выйти </span>
        </div>

    </div>
</template>

<script>

import { getUserData, logoutUser} from '../api.js';
import UserOrders from '../components/UserOrders.vue';
import UserPromo from '../components/UserPromo.vue';
import UserAddress from '../components/UserAddress.vue';
import UserSettings from '../components/UserSettings.vue';


export default {
    name: 'ProfilePage',
    data () {
        return {
            orders_component_active: true,
            promo_component_active: false,
            address_component_active: false,
            settings_component_active: false,
        }
    },
    components: {
        UserOrders,
        UserPromo,
        UserAddress,
        UserSettings,
    },
    computed: {
        is_user_authorized () {
            return this.$store.state.is_user_authorized;
        },
        user () {
            return this.$store.state.user;
        },
    },
    async beforeMount () {
        if (Object.keys(this.user).length === 0) {
            var success = await this.getUserData()
            console.log('get user status: ', success)
            if (!success) {
                this.$store.commit('setUserAuthorized', false)
                this.$router.push('/')
            }
        }
    },
    methods: {
        getUserData,
        logoutUser,
        async logout_user_clicked () {
            await logoutUser();
            this.$store.commit('setUserAuthorized', false)
            this.$router.push('/')
        },
        setUserPageComponentActive (component) {
            if (component == 'orders') {
                this.orders_component_active = true
                this.promo_component_active = false
                this.address_component_active = false
                this.settings_component_active = false
            } else if (component == 'promo') {
                this.orders_component_active = false
                this.promo_component_active = true
                this.address_component_active = false
                this.settings_component_active = false
            } else if (component == 'address') {
                this.orders_component_active = false
                this.promo_component_active = false
                this.address_component_active = true
                this.settings_component_active = false
            } else if (component == 'settings') {
                this.orders_component_active = false
                this.promo_component_active = false
                this.address_component_active = false
                this.settings_component_active = true
            } 
        }
    }
}
</script>