<script>

import DesktopCategoryItem from '../components/DesktopCategoryItem.vue';
import { goToContactPage } from '../common_methods.js';

export default { 
    name: 'MainHeader',
    components: {
        DesktopCategoryItem,
    },
    created() {
        window.addEventListener('scroll', this.onScroll);
    },
    unmounted () {
        window.removeEventListener('scroll', this.onScroll);
    },
    computed: {
        mobile_main_menu_show () {
            return this.$store.state.mobile_main_menu_show;
        },
         is_user_authorized () {
            return this.$store.state.is_user_authorized;
        },
        categories () {
            return this.$store.state.categories
        },
        cart_amount() {
            return this.$store.state.cart_amount;
        },
        cart_quantity () {
            return Object.keys(this.$store.state.products_in_cart).length;
        },
        popup_login_show () {
            return this.$store.state.popup_login_show;
        },
    },
    methods: {
        goToContactPage,
        openLoginUser () {
            this.toggle_mobile_main_menu()
            this.$store.commit('setPopupLogin', true)
        },
        goToStock () {
            this.$router.push('/stock')
        },
        goToMainPage () {
            this.$router.push('/')
        },
         goToPage (page_link) {
            this.toggle_mobile_main_menu();
            this.$router.push(page_link)
        },
        onScroll () {
            this.checkMenu()
        },
        checkMenu() {
            // get dekstop header and check it to make fixed
            var header = document.getElementsByClassName("header_desktop_main_wrapper")[0];

            if (header) {
                var sticky = header.offsetTop;
                if (window.pageYOffset > sticky) {
                    header.classList.add("fixed");
                } else {
                    header.classList.remove("fixed");
                }
            }
            // get mobile header and check it to make fixed
            var header_mobile = document.getElementsByClassName('mobile_category_swiper')[0];
            if (header_mobile) {
                var sticky_mobile = header_mobile.offsetTop;
                if (window.pageYOffset > sticky_mobile) {
                    header_mobile.classList.add("fixed");
                } else {
                    header_mobile.classList.remove("fixed");
                }
            }
        },
        toggle_mobile_main_menu () {
            if (this.mobile_main_menu_show) {
                this.$store.commit('setMobileMainMenu', false);
            } else {
                this.$store.commit('setMobileMainMenu', true);
            }
            // var el = document.getElementsByClassName('mobile_header_main')[0].classList.toggle("show")
            // document.getElementsByTagName('body')[0].classList.toggle('overflow_disable')
            // console.log(el);
        },
        openLoginPopup () {
            this.$store.commit('setPopupLogin', true);
        },
        openCallPhonePopup () {
            this.$store.commit('setPopupCallPhone', true);
        }
    },
}
</script>