<script>

import { tryRegisterUser, isUserAuthorized } from '../api.js';

export default {
    name: 'RegisterPopup',
    data () {
        return {
            user_phone: '79782215509',
            user_password: '343845',
            user_name: 'maks',
            error_message: '',
            verifying_code: false,
            user_verify_code: null,
        }
    },
    computed: {
        popup_register_show () {
            return this.$store.state.popup_register_show;
        }
    },
    methods: {
        isUserAuthorized,
        tryRegisterUser,
        closeRegisterPopup () {
            this.$store.commit('setPopupRegister', false);
        },
        async try_user_register () {
            this.error_message = ''    
            console.log('user want to register', 
            this.user_name,
            this.user_phone, 
            this.user_password)
            var response = await tryRegisterUser(
                this.user_name,
                this.user_phone,
                this.user_password,
                this.user_verify_code,
                'check_account',
            )
            console.log('resonse is', response);
            console.log('resonse msg is', response.msg);
            if (response.result == false) {
                console.log('we sucked');
                this.error_message = response.msg
            } else if (response.result == true) {
                console.log('it is ok, code is sent')
                this.verifying_code = true;
            }
        },
        async verify_user_register_clicked () {
            this.error_message = '' 
            console.log('user want to verify register', 
            this.user_name,
            this.user_phone, 
            this.user_password)
            var response = await tryRegisterUser(
                this.user_name,
                this.user_phone,
                this.user_password,
                this.user_verify_code,
                'verify_account',
            )
            console.log('response is', response);
            if (response.result == false) {
                this.error_message = response.msg;
            } else if (response.result == true) {
                console.log('user is registered!');
                this.closeRegisterPopup();
                this.$router.push('/profile')
            }
        }
        // async try_user_login () {
