<template>
    <div class="offer_page_wrapper">
        <!-- products in cart are: 
        {{ this.products_in_cart }}

        <div>{{ this.payment_method }}</div>
        <div>{{ this.delivery_method }}</div>
        <div>{{ this.order_name }}</div>
        <div>{{ this.order_phone }}</div>
        <div>{{ this.order_address }}</div> -->
        
        <div class="cart__main__title">
            <div class="nice__title2__left">
                Оформление заказа
            </div>
        </div>

        <div class="offer__page__container">
        <!-- offer page user info -->
        <OfferPageInfo />
        <OfferPageSubmit />
        <!-- offer page user info -->
        </div>

    </div>
</template>

<script>

import { submitOrderAPI, getProductsInCart } from '../api.js';

import OfferPageInfo from '../components/OfferPageInfo.vue';
import OfferPageSubmit from '../components/OfferPageSubmit.vue';

export default {
    name: 'OfferPage',
    data () {
        return {
            delivery_method: '',
            payment_method: '',
            order_name: '',
            order_phone: '',
            order_address: '',
            error_msg: '',
        }
    },
    components: {
        OfferPageInfo,
        OfferPageSubmit,
    },
    computed: {
        products_in_cart () {
            return this.$store.state.products_in_cart;
        }
    },
    methods: {
        submitOrderAPI,
        getProductsInCart,
        checkOrderData () { 
            if (!this.order_name) {
                this.error_msg = 'Введите ваше имя'
                return false;
            } else if (!this.order_phone) {
                this.error_msg = 'Введите номер телефона'
                return false;
            } else if (!this.delivery_method) {
                this.error_msg = 'Выберите способ доставки'
                return false;
            } else if (!this.payment_method) {
                this.error_msg = 'Выберите способ оплаты'
                return false;
            } else if (this.delivery_method == 'courier' && !this.order_address) {
                this.error_msg = 'Введите адрес доставки'
                return false;
            }
            return true;
        },
        async submitOrder () {
            var can_create_order = this.checkOrderData()
            if (can_create_order) {
                console.log('can create: ', can_create_order);
                console.log('try to submit order...')
                // console.log('name, phone: ', this.order_name, this.order_phone);
                // console.log('address: ', this.order_address);
                // console.log('delivery, payment: ', this.delivery_method, this.payment_method);
                var is_order_created = await this.submitOrderAPI(
                    this.order_name, 
                    this.order_phone, 
                    this.order_address, 
                    this.delivery_method, 
                    this.payment_method
                );
                if (is_order_created) {
                    // window.alert('success')
                    // go to main page
                    this.$router.push('/')
                    // open thank popup
                    this.$store.commit('setPopupOrderCreated', true);
                    // update products in cart
                    this.getProductsInCart();
                }
                console.log('order created: ', is_order_created)
            } else {
                console.log('can not create order');
                // show popup error message
                this.$store.commit('showPopupErrorDialog', this.error_msg)
            }
        }
    }
}
</script>
