<template>
    <div class="offer__page__block2__content__item">

        <div class="offer__page__block2__content__item__first">
        
            <div class="offer__page__block2__content__item__prinfo">
                <img :src="this.$store.state.STATIC_URL + this.cart_product.imgsrc" alt="">
                <div class="offer__page__block2__content__item__prinfo__second">
                    <a class="offer__page__block2__content__item__prinfo__second__name"
                    href="{% url 'products:product' item.product.id %}">
                        {{ this.cart_product.name }}
                    </a>
                    <span class="offer__page__block2__content__item__prinfo__second__name">
                        1 шт.
                    </span>
                </div>
            </div>
        </div>
        <div class="offer__page__block2__content__item__second">
            <span>
                {{ this.cartItemPrice() }}
            </span>
                <!-- <span>
                    pr sale price
                </span>
                    <span>
                        pr price
                    </span> -->
        </div>

    </div>

</template>

<script>
export default {
    name: "OfferPageSubmitItem",
    props: {
        cart_product: {},
    },
    computed: {
        products_in_cart () {
            return this.$store.state.products_in_cart;
        },
    },
    methods: {
        cartItemQuantity(current_item) {
            var quantity = this.products_in_cart[current_item['id']]
            return quantity
        },
        getProductPrice(product) {
            if (product.sale_price > 0) {
                return product.sale_price
            }
            return product.price
        },
        cartItemPrice() {
            var quantity = this.cartItemQuantity(this.cart_product)
            var price = this.getProductPrice(this.cart_product)
            return quantity * price
        }
    }
}
</script>