<template>


<div class="mgt10 mobile_categories_main">

    <swiper
        :slides-per-view="'auto'"
        @swiper="setControlledSwiper"
        class="mobile_category_swiper"
        :centeredSlides="true" 
        :centeredSlidesBounds="true"
        :autoHeight="true"
        :space-between="3"
    >
        <!-- width="75" -->
        <!-- :setWrapperSize="true"
        :centeredSlides="true"
        :centerInsufficientSlides="true" -->
        <!-- :a11y="true" -->
    <swiper-slide 
    @click="this.goToCategory(category.id)"
    v-for="(category, index) in this.categories"
    :key="index"
    :class="category.id == this.active_category_id ? 'active': ''"
    class="mobile_category_swiper_item pointer noselect"
    >
        <div>
            {{ category.name }}
        </div>
    </swiper-slide>

  </swiper>

  <!-- <button
  @click="this.do_click()">
      click
  </button>

  <div>
      active category id: {{ this.active_category_id }}
  </div>

  <div>
      {{ this.categories }}
  </div> -->

</div>    


</template>

<script>

import SwiperCore, { Controller, A11y } from 'swiper';
import { Swiper, SwiperSlide } from 'swiper/vue';
import 'swiper/swiper.scss';

SwiperCore.use([Controller, A11y]);

export default {
    name: 'MobileCategorySlider',
    // data() {
    //   return {
    //     mobileCategorySwiper: null,
    //   };
    // },
    components: {
        Swiper,
        SwiperSlide,
    },
    computed: {
        active_category_id () {
            return this.$store.state.activeCategory.id;
        },
        mobileCategorySwiper () {
            return this.$store.state.mobileCategorySwiper;
        },
        api_url () {
            return this.$store.state.STATIC_URL;
        },
        categories () {
            return this.$store.state.categories;
        }
    },
    methods: {
        goToCategory (category_id) {
            var cat_to_scroll = document.getElementById('category-'+category_id)
            cat_to_scroll.scrollIntoView({behavior: "smooth"})
        },
        do_click() {
            console.log(this.mobileCategorySwiper);
            this.mobileCategorySwiper.slideTo(6, 1500)
        },
        setControlledSwiper(swiper) {
            this.$store.commit('setMobileCategorySwiper', swiper)
      },
    }
}
</script>