<template>
    <div class="login_popup"
    v-show="this.popup_login_show">
        <div class="login_popup_block">
            <div class="login_popup_title">
                Вход на сайт
            </div>
            <div class="error_message"
            v-show="this.error_message.length > 0">
                <span> 
                {{ error_message }}
                </span>
            </div>
            <div class="main_input_title">
                Номер телефона
            </div>
            <input class="main_input"
            placeholder="+7" v-model="user_phone" type="text">
            <div class="main_input_title">
                Пароль
            </div>
            <input class="main_input"
            placeholder="" v-model="user_password" type="password">
            <div class="login_popup_submit">
                <span class="noselect"
                @click="this.try_user_login()">
                    Войти
                </span> 
            </div>
            <div class="login_popup_submit">
                <span class="noselect btn_passive"
                @click="this.goToRegister()">
                    Регистрация
                </span> 
            </div>
        </div>
        <svg class="login_popup_close pointer"
        v-on:click="this.closeLoginPopup()"
        width="25" height="25" viewBox="0 0 25 25" fill="none" xmlns="http://www.w3.org/2000/svg">
            <path fill-rule="evenodd" clip-rule="evenodd" d="M9.84606 12.4986L0.552631 3.20519C-0.1806 2.47196 -0.1806 1.28315 0.552631 0.549923C1.28586 -0.183308 2.47466 -0.183308 3.20789 0.549923L12.5013 9.84335L21.792 0.552631C22.5253 -0.1806 23.7141 -0.1806 24.4473 0.552631C25.1805 1.28586 25.1805 2.47466 24.4473 3.20789L15.1566 12.4986L24.45 21.792C25.1832 22.5253 25.1832 23.7141 24.45 24.4473C23.7168 25.1805 22.528 25.1805 21.7947 24.4473L12.5013 15.1539L3.20519 24.45C2.47196 25.1832 1.28315 25.1832 0.549923 24.45C-0.183308 23.7168 -0.183308 22.528 0.549923 21.7947L9.84606 12.4986Z" fill="white"></path>
        </svg>
    </div>
    <div class="login_popup_wrapper"
    v-on:click="this.closeLoginPopup()"
    v-show="this.popup_login_show">
    </div>
</template>

<script>

import { tryLoginUser, isUserAuthorized } from '../api.js';

export default {
    name: 'LoginPopup',
    data () {
        return {
            user_phone: '79782215509',
            user_password: '343845',
            error_message: '',
        }
    },
    computed: {
        popup_login_show () {
            return this.$store.state.popup_login_show;
        }
    },
    methods: {
        isUserAuthorized,
        tryLoginUser,
        goToRegister () {
            this.closeLoginPopup();
            this.$store.commit('setPopupRegister', true)
        },
        closeLoginPopup () {
            this.$store.commit('setPopupLogin', false);
        },
        async try_user_login () {
            this.error_message = ''
            console.log('user want to login', this.user_phone, this.user_password)
            let response = await this.tryLoginUser(this.user_phone, this.user_password)
            if (response.result == true) {
                this.closeLoginPopup();
                this.isUserAuthorized();
                this.$router.push('/profile');
            } else if (response.result == false) {
                this.error_message = response.msg;
            }
        },
    }
}

</script>