import axios from 'axios';
/* import ENUM from './enums'; */


/* user data */
export async function isUserAuthorized () {
    await axios.get(
        '/is_user_authorized_api',
    ).then((response) => {
        var is_authorized = response.data.is_authorized
        this.$store.commit('setUserAuthorized', is_authorized)
    })
}
/* try login user */
export async function tryLoginUser (username, password) {
    let result;
    let msg;
    await axios.get(
        '/login_user_ajax', {
            params: {
                'username': username,
                'password': password,
            }
        }
    ).then((response) => {
        var success = response.data.success;
        var message = response.data.message;
        result = success;
        msg = message;
    })
    return {result, msg};
}
/* try register user */
export async function tryRegisterUser (user_name, phone, password, user_code, request_type) {
    let result;
    let msg;
    await axios.get(
        '/try_register_user_ajax', {
            params: {
                'name': user_name,
                'username': phone,
                'password': password,
                'user_code': user_code,
                'type': request_type,
            }
        }
    ).then((response) => {
        var success = response.data.success;
        var message = response.data.message;
        result = success;
        msg = message;
    })
    return {result, msg};
}


