export async function getUserData () {
    let status;
    let user;
    await axios.get(
        '/get_user_data_ajax',
    ).then((response) => {
        status = response.data.status;
        user = response.data.user;
    })
    if (status) {
        this.$store.commit('setUser', user)
        return true;
    } else {
        return false;
    }
}

/* create order */

export async function submitOrderAPI(
    name, phone, address, delivery_method, payment_method
) {
    let created_status;
    await axios.get(
        '/submit_order_ajax', {
            params: {
                'name': name,
                'phone': phone,
                'address': address,
                'delivery_method': delivery_method,
                'payment_method': payment_method,
            }
        }
    ).then((response) => {
        created_status = response.data.order_created
    })
    return created_status;
}

