<template>
    <div class="stock_page_wrapper">
        <div class="nice__title2__left">
            Акции
        </div>
        <div class="stock__cards">
            <StockItem 
            v-for="stock in this.stocks"
            :key="stock"
            :current_stock="stock"
            />
        </div>
    </div>
</template>

<script>

import { getStocks } from '../api.js';

import StockItem from '../components/StockItem.vue';
export default {
    name: "StockPage",
    components: {
        StockItem
    },
    async beforeMount () {
        if (this.stocks.length == 0) {
            var stocks = await getStocks();
            this.$store.commit('setStocks', stocks)
        }
    },
    computed: {
        stocks () {
            return this.$store.state.stocks;    
        }
    },
    methods: {
        getStocks,
    }
}
</script>