<template>

<div 
v-show="this.popup_error_dialog_show"
class="popup_error_dialog_container">
	<div class="popup_error_dialog_content">
		{{ this.msg }}
	</div>
</div>


</template>


<script>
export default {
	name: 'PopupErrorDialog',
	computed: {
		popup_error_dialog_show () {
			return this.$store.state.popup_error_dialog_show
		},
		msg () {
			return this.$store.state.popup_error_dialog_message;
		}
	}
}
</script>
