import { createApp } from 'vue' ;
import { createWebHistory, createRouter } from "vue-router";
import App from './App.vue'
import { createStore } from 'vuex'
import VueLazyLoad from 'vue3-lazyload'


/* import NotFoundComponent from './components/NotFoundComponent.vue'; */
import ENUM from './enums.js';

/* import ProductPageModal from './components/ProductPageModal.vue'; */
import ProductPageModal from './components/ProductPageModal.vue';
import MainPage from './components/MainPage.vue';
import CartPage from './components/CartPage.vue';
import OfferPage from './components/OfferPage.vue';
import ProfilePage from './components/ProfilePage.vue';
import StockPage from './components/StockPage.vue';
import StockItemPage from './components/StockItemPage.vue';





const routes = [
  {
    path: '/',
    name: 'MainPage',
    component: MainPage,
    children: [
        {
          path: '/product/:product_id',
          name: 'ProductPageModal',
          component: ProductPageModal,
          props: true,
        },
    ],
  },
  {
    path: '/cart',
    name: 'CartPage',
    component: CartPage,
  },
  {
    path: '/offer',
    name: 'OfferPage',
    component: OfferPage,
  },
  {
    path: '/profile',
    name: 'ProfilePage',
    component: ProfilePage,
  },
  {
    path: '/stock',
    name: 'StockPage',
    component: StockPage,
  },
  {
    path: '/stock/:stock_id',
    name: 'StockItemPage',
    component: StockItemPage,
    props: true,
  },
];

const router = createRouter({
  history: createWebHistory('/app'),
  routes,
});



/* export default router; */



const store = createStore({
  state () {
    return {
      delivery_phone: '+79782215509',
      delivery_phone_display: '7 978 221-55-09',
      /* STATIC_URL: 'http://vuetest.fast-code.ru/', */
      STATIC_URL: 'http://127.0.0.1:8000/',
      /* STATIC_URL: 'http://192.168.1.141:8000/', */
      apiState: ENUM.INIT,
      cart_amount: 0,
      products: [],
      categories: [],
      products_in_cart: {},
      product: {},
      stocks: [],
      activeCategory: {},
      current_product: {},
      product_attributes: {},
      popup_message_title: '',
      popup_message_show: false,
      popup_error_dialog_show: false,
      popup_error_dialog_message: '',
      popup_login_show: false,
      popup_register_show: false,
      popup_call_phone_show: false,
      popup_order_created_show: false,
      mobile_main_menu_show: false,
      /* user */
      is_user_authorized: false,
      /* mobile category swiper */
      mobileCategorySwiper: null,
      user: {},
    }
  },
  mutations: {
    setApiState(state, apiState) {
      this.state.apiState = apiState;
    },
    setMobileMainMenu(state, is_show) {
      this.state.mobile_main_menu_show = is_show;
    },
    setStocks (state, stocks_data) {
      this.state.stocks = stocks_data
    },
    setPopupOrderCreated (state, is_show) {
      this.state.popup_order_created_show = is_show
    },
    setMobileCategorySwiper (state, swiper) {
      this.state.mobileCategorySwiper = swiper;
    },
    setUser(state, user) {
      this.state.user = user;
    },
    setUserAuthorized (state, is_authorized) {
      this.state.is_user_authorized = is_authorized;
    },
    setPopupLogin(state, popup_login) {
      this.state.popup_login_show = popup_login;
    },
    setPopupRegister(state, popup_register) {
      this.state.popup_register_show = popup_register;
    },
    setPopupCallPhone(state, popup_phone) {
      this.state.popup_call_phone_show = popup_phone;
    },
    set_activeCategory(state, active_category) {
      this.state.activeCategory = active_category;
      /* console.log('active category is', active_category) */
      if (this.state.mobileCategorySwiper) {
        /* var category_index = this.state.categories.filter(cat => cat.id == active_category.id)[0] */
        var category_index = this.state.categories.indexOf(active_category)
        this.state.mobileCategorySwiper.slideTo(category_index, 1500)
        /* console.log(this.state.mobileCategorySwiper) */
        /* this.state.mobileCategorySwiper. */

      }
    },
    setProductAttribute(state, attr) {
      var attribute = attr['attribute']
      var attribute_value = attr['value']
      var attribute_lvl = attr['lvl']
      console.log('starting to set attrs', attribute, attribute_value, attribute_lvl)
      state.product_attributes[attribute] = {
        'value': attribute_value,
        'lvl': attribute_lvl
      }
      console.log('from state: ', state.product_attributes)
    },
    stashProductAttributes(state) {
      state.product_attributes = {}
    },
    showPopupMessage(state, message) {
      state.popup_message_title = message;
      state.popup_message_show = true;
      setTimeout( () => {
        this.state.popup_message_show = false;
      }, 2000);
    },
    showPopupErrorDialog(state, message) {
      state.popup_error_dialog_message = message;
      state.popup_error_dialog_show = true;
      setTimeout( () => {
        this.state.popup_error_dialog_show = false;
      }, 2000);
    },
    change_count(state, count) {
      state.count = count
    },
    setCategories(state, categories) {
        categories.forEach( category => state.categories.push(category))
    },
    setProducts(state, products) {
        products.forEach( product => state.products.push(product))
    },
    setProductsInCart(state, products_in_cart) {
        if (products_in_cart.length == 0) {
          this.state.products_in_cart = {}
        } else {
        products_in_cart.forEach( product_in_cart => 
        state.products_in_cart[product_in_cart['product_id']] = product_in_cart['quantity'])
        }
    },
    addProductToCart(state, product_id) {
      state.products_in_cart[product_id.toString()] = 1
    },
    addProductQuantity(state, product_id) {
      state.products_in_cart[product_id.toString()] += 1
    },
    deleteProductFromCart(state, product_id) {
      delete state.products_in_cart[product_id.toString()]
    },
    deleteProductQuantity(state, product_id) {
    state.products_in_cart[product_id.toString()] -= 1
    },
    setCartAmount(state, cart_amount) {
      state.cart_amount = cart_amount;
    },
    setProduct(state, product) {
      state.product = product;
    },
    setCurrentProduct(state, product) {
      console.log('call set current product', product)
      state.current_product = product;
    },
  },
  getters: {
    getProductByid: (state) => (product_id) => {
      var product = state.products.find(pr => pr['id'] == product_id);
      return product;
    },
    getCartProducts(state){
      var keys = Object.keys(state.products_in_cart);
      var cart_products = []
      state.products.forEach( product => {
        var id = product.id.toString()
        if (keys.includes(id)) {
          cart_products.push(product);
        }
      })
      return cart_products;
    },
  }
})


/* const router = createRouter({ history: createWebHistory(), routes}) */
const app = createApp(App)
app.use(store)
app.use(router)
app.use(VueLazyLoad)
/* app.use(checkView) */

app.mount('#app')
